import React from "react"
import Plant from "../components/plant.js"
import { Collapse, Modal, Button, Popover, OverlayTrigger } from "react-bootstrap"
import Tree from "../../static/apple-tree.svg"
import Apple from "../../static/apple.svg"

import styles from "../components/messageboard.module.css"

class MessageBoard extends React.Component {
	constructor(props) {
		super();

       	if (typeof window !== 'undefined') {
			if(localStorage.getItem("savedMessages")==null){
				localStorage.savedMessages = JSON.stringify([
					"Example: I'm grateful for the people in my life.", 
					"Example: I'm grateful that I found a bracelet on the ground!",
					"Example: I'm happy that I saw a squirel today.",
					"Example: Today, I saw my friends online.",
					"Example: I finished a good TV show.",
				]);
			};
			this.state = {
		    	"messages": JSON.parse(localStorage.getItem("savedMessages"))
		    };
		} else {
			this.state = {
		    	"messages": ["Goodiest"]
		    };
		}

		this.state = {"messages": this.state.messages, "open": false, "show": false};


	    this.enterPressed = this.enterPressed.bind(this);
	    this.createMessage = this.createMessage.bind(this);
	    this.handleClose = () => this.setState({"show":false});
	    this.handleShow = () => this.setState({"show":true});

	    this.popover = [(
			<Popover className={styles.popover} id="popover-basic">
				<Popover.Content>
					{this.state.messages[this.state.messages.length-1]}
				</Popover.Content>
			</Popover>
		), (
			<Popover className={styles.popover} id="popover-basic">
				<Popover.Content>
					{this.state.messages[this.state.messages.length-2]}
				</Popover.Content>
			</Popover>
		), (
			<Popover className={styles.popover} id="popover-basic">
				<Popover.Content>
					{this.state.messages[this.state.messages.length-3]}
				</Popover.Content>
			</Popover>
		), (
			<Popover className={styles.popover} id="popover-basic">
				<Popover.Content>
					{this.state.messages[this.state.messages.length-4]}
				</Popover.Content>
			</Popover>
		), (
			<Popover className={styles.popover} id="popover-basic">
				<Popover.Content>
					{this.state.messages[this.state.messages.length-5]}
				</Popover.Content>
			</Popover>
		)];
	}

	enterPressed(event) {
	    var code = event.keyCode || event.which;
	    if(code === 13 && event.target.value !== '') { //13 is the enter keycode
	        this.state.messages.unshift(event.target.value);
	    	this.setState({"messages": this.state.messages});
	   //  	if (typeof window !== 'undefined') {
				// localStorage.savedMessages = JSON.stringify(this.state.messages); 
	   //  	}
	    	event.target.value = '';
	    } 
	}

	createMessage(message) {
		return (
			<li>
				{message}
			</li>
		);
	}

	render() {
		return (
			<div>
				<Button style={this.state.show ? {display:"none"} : {}} className={styles.board} onClick={this.handleShow}>Water The Tree</Button>
				<div className={styles.plant}>
					<Tree className={styles.tree}/>
					<OverlayTrigger trigger="hover" placement="top" overlay={this.popover[0]}>
						<Apple className={styles.apple1}/>
					</OverlayTrigger>
					<OverlayTrigger trigger="hover" placement="top" overlay={this.popover[1]}>
						<Apple className={styles.apple2}/>
					</OverlayTrigger>
					<OverlayTrigger trigger="hover" placement="top" overlay={this.popover[2]}>
						<Apple className={styles.apple3}/>
					</OverlayTrigger>
					<OverlayTrigger trigger="hover" placement="top" overlay={this.popover[3]}>
						<Apple className={styles.apple4}/>
					</OverlayTrigger>
					<OverlayTrigger trigger="hover" placement="top" overlay={this.popover[4]}>
						<Apple className={styles.apple5}/>
					</OverlayTrigger>
				</div>

				<Modal show={this.state.show} onHide={this.handleClose}>
					<Modal.Header closeButton>
						<Modal.Title>Type in something you are grateful for! </Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<input 
							type="text"
							placeholder="I'm grateful for..."
							onKeyPress={this.enterPressed.bind(this)}
						/>

						<Collapse in={this.state.open}>
							<ul> {this.state.messages.map((message) => this.createMessage(message))} </ul>
						</Collapse>

					</Modal.Body>
					<Modal.Footer>
						<Button
							variant="secondary" 
							onClick={() => this.setState({ "open": !this.state.open })}
							aria-controls="example-collapse-text"
						>{this.state.open ? "Collapse" : "Past Notes"}</Button>
					</Modal.Footer>

					<Plant></Plant>
				</Modal>
			</div>
	  	)
	}
}

export default MessageBoard


